
.wrapper{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 30px 0 0 0;
}

.itemContainer{
    width: 400px;
    max-height: 400px;
    margin: 10px auto;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.itemImage{
    width: 350px;
    height: 200px;
    

}

.itemImageContainer{
    width: 350px;
    height: 200px;
    display: flex;
    justify-content: center;
}

.itemTitle{
    font-weight: 700;
    font-style: 30px;
    text-align: left;
    width: 350px;
    margin: 5px 0;
}

.itemDescription{
    width: 350px;
}

a{
    text-decoration: none;
    color: black;
}

.readMore{
    color: rgb(5, 85, 206);
    margin: 5px 0;
}