.profileForm {
    width: 80%;
    display: flex;
    justify-content: center;
    margin: 20px 0;

}
.label {
    margin: 10px 0;
}

.input {
    width: 300px;
    height: 20px;
    display: flex;
    margin: 5px 0;
    resize: none;
}
.inputContainer{
    display: flex;
    width: 500px;
    justify-content: space-between;
}


.saveButton {
    width:40%;
    height: 40px;
    background-color: rgb(5, 85, 206);
    border-radius: 8px;
    color: #fff;
    font-size: 20px;
    margin: 20px 0 0 0;

}

.saveButton:hover {
    filter: brightness(1.2);
}

.disabledButton{
    width: 40%;
    height: 40px;
    background-color: rgb(203, 204, 207);
    border-radius: 8px;
    color: #fff;
    font-size: 20px;
    margin: 20px 0 0 0;
}

