.wrapper {
    display: flex;
    height: 100%;
}



.logo {
    width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    color: rgb(5, 55, 126);
    font-size: 70px;
    font-weight: 700;
}
.logoImage {
    max-width: 100%;
}

.input {
    width: 300px;
    height: 300px;
    margin: auto;
    color: rgb(5, 55, 126);
    border: 1px solid rgb(112, 114, 117);
    border-radius: 8px;
    background-color: rgba(188, 188, 204, 0.226);
    display: flex;
}

.loginForm{
    height: 180px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: auto;
}

label{
    padding: 0 0 10px 0;
}

.field {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    flex-direction: column;
}

.buttonContainer {
    display: flex;
    justify-content: center;
}

.submitButton {
    width: 100%;
    height: 40px;
    background-color: rgb(5, 85, 206);
    border-radius: 8px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 20px;
}
.disabledButton{
    width: 100%;
    height: 40px;
    background-color: rgb(192, 195, 201);
    border-radius: 8px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 20px;
}
.submitButton:hover {
    filter: brightness(1.2);
}

.error {
    color: red;
}


@media (max-width:900px) {
    .wrapper{
        display: flex;
        flex-direction: column;
        height: 100vh;
        justify-content: center;
        align-items: center;
        
    }
    .logoImage{
        width: 300px;
       

    }
    .logoTitle {
        font-size: 30px;
        width: 300px;
        margin: auto;

    }
    .logo {
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px 0;

    }
    .loginForm{
        width: 300px;
        height: 100%;

    }
    .input {
        width: 80%;
        margin: 10px;

    }
    .field {
      font-size: 25px;
    }
    input{
        height: 25px;
    }
}